import Link from 'next/link'
import fetch from 'isomorphic-unfetch';
import Card from '../components/BlogCard/BlogCard'

const Post = (props) => (
        <Link as={`/${props.slug}`} href={`/blogpost?title=${props.title}`}>
            <a>{props.title}</a>
        </Link>
)

const Index = (props) => {
    return !props.postContent[0] ?
    (<h1 style={{fontFamily: 'Montserrat'}}>Loading...</h1>)
    :
    (
        <div className='post' style={{fontFamily: 'Montserrat'}}>
            <h1>My blog - by me</h1>
            <ul style={{listStyleType: 'none'}}>
                {
                    props.postContent.map((post, i) => (
                        <li key={post._id} style={{margin: '10px', width: '50%'}}>
                            <Card>
                                <Post 
                                    slug={i}
                                    title={post.title}
                                />
                                <br/>
                                <span>{post.body.slice(0, 20)}...</span>
                            </Card>
                        </li>
                            
                    ))
                }
            </ul>

            <style jsx>
                {`
                    @import url('<link href="https://fonts.googleapis.com/css?family=Montserrat" rel="stylesheet">')
                    .post {
                        font-family: sans-serif
                    }
                `}
            </style>
        </div>
    )
}

Index.getInitialProps = () => {
    return fetch(`http://localhost:3001/home`)
        .then(response => {
            return response.json()
        })
        .then(posts => {
            return {postContent: posts.reverse()}
        })
        .catch(err => console.log(err))
} 

export default Index